<template>
  <van-popup v-model="showTechnologyPopup" position="right" :overlay="false" style="height:100vh;width: 100vw;overflow-y: scroll">
    <div class="popup-header"></div>
    <div class="cell-flex set-margin">
      <div>: {{technologyObj.year}}</div>
      <van-button size="small" type="primary" @click="chooseTechnologyYear"></van-button>
    </div>
    <div class="cell-flex set-margin">
      <div>: {{technologyObj.term}}</div>
      <van-button size="small" type="primary" @click="chooseTechnologyTerm"></van-button>
    </div>
    <el-cascader
      :options="technologySelections"
      v-model="selectedOptions">
    </el-cascader>
    <van-row class="btn-position">
      <van-col span="12">
        <van-button bottom-action @click="updateTechnology"></van-button>
      </van-col>
      <van-col span="12">
        <van-button type="primary" bottom-action @click="cancelUpdateTechnology"></van-button>
      </van-col>
    </van-row>
    <van-popup position="bottom" transition="popup-slide" style="width: 100vw;height: 30vh" v-model="showPicker">
      <van-picker :columns="pickerColumn" show-toolbar @confirm="pickerConfirm"></van-picker>
    </van-popup>
  </van-popup>
</template>

<script>
import axios from 'axios';
export default {
  name: 'TechnologyPopup',
  props: ['showTechnologyPopup', 'technologySelections'],
  data() {
    return {
      pickerColumn: [],
      yearColumn: ['2014','2015','2016','2017','2018','2019','2020'],
      termColumn: ['', '',''],
      selectedOptions: [],
      technologyObj: {
        name: '',
        weight: 0,
        year: '',
        term: '',
      },
      technologys: [],
      showPicker: false,
    }
  },
  methods: {
    chooseTechnologyYear() {
      this.pickerColumn = this.yearColumn.concat();
      this.showPicker = true;
    },
    chooseTechnologyTerm() {
      this.pickerColumn = this.termColumn.concat();
      this.showPicker = true;
    },
    pickerConfirm(val) {
      if (isNaN(parseInt(val))) {
        this.technologyObj.term = val;
      } else {
        this.technologyObj.year = val;
      }
      this.showPicker = false;
    },
    findWeight(array) {
      if (array === []) {
        return 0;
      }
      for (const item of this.technologySelections) {
        if (array[0] === item.value) {
          if (array[1]) {
            for (const child of item.children) {
              if (array[1] === child.value) {
                return child.weight;
              }
            }
          }
          return item.weight;
        }
      }
    },
    async updateTechnology() {
      this.technologyObj.weight = this.findWeight(this.selectedOptions);
      this.technologyObj.name = this.selectedOptions.join('/');
      const res = await axios.post('/technology/updateTechnologys', {
        openId: localStorage.getItem('userID'),
        technology: this.technologyObj,
      });
      if (res.data.message === 'ok') {
        this.$message({
          type: 'success',
          message: ''
        })
        this.cancelUpdateTechnology();
        this.$emit('reloadTechnologys');
      }
    },
    cancelUpdateTechnology() {
      this.technologyObj = {
        year: '',
        weight: 0,
        term: '',
        name: ''
      };
      this.$emit('closeTechnologyPopup');
    }
  }
}
</script>

<style scoped>
.cell-flex {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
}
.set-margin {
  margin-bottom: 15px;
}
.btn-position {
  margin-top: 20px;
}
.popup-header {
  font-size: 1.5625rem;
  padding: 10px;
  letter-spacing: 5px;
}
</style>



